body {
	background-color: #f2f2f2;
}

.movies .movie {
	width: 45%;
	background-color: white;
	margin-bottom: 70px;
	display: flex;
	align-items: flex-start;
	/* justify-content: space-between; */
	font-weight: 300;
	padding: 20px;
	border-radius: 5px;
	color: #b2b3bc;
	box-shadow: 0 30px 60px -12px rgb(50 50 93 / 25%),
		0 18px 36px -18px rgb(0 0 0 / 30%),
		0 -12px 36px -8px rgb(0 0 0 / 3%);
}

.movie img {
	position: relative;
	top: -50px;
	max-width: 120px;
	margin-right: 30px;
	box-shadow: 0 30px 60px -12px rgb(50 50 93 / 25%),
		0 18px 36px -18px rgb(0 0 0 / 30%),
		0 -12px 36px -8px rgb(0 0 0 / 3%);
}

.movie .movie__title,
.movie .movie__year {
	margin: 0;
	font-weight: 300;
	color: #2c2c2c;
}

.movie h3 {
	margin-bottom: 5px;
	font-size: 24px;
}

.movie .movie__genres {
	list-style: none;
	padding: 0;
	margin: 0;
	display: flex;
	margin: 5px 0px;
}

.movie__genres li {
	margin-right: 10px;
	font-size: 14px;
}